@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+TC:wght@100;300;400;500;700;900 &display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
    padding: 0;
    margin: 0;
    list-style: none;
    text-decoration: none;
    font-family: 'Noto Sans TC', sans-serif;
    color: #000;
    color: var(--black);
}

:root {
    --white: #fff;
    --black: #000;
}


@media screen and (max-width: 576px) {
    * {
        font-size: 12px;
    }
}

@media screen and (max-width: 1068px) {
    * {
        font-size: 14px;
    }
}


input:focus,
button:focus {
    outline: none;
}

div {
    display: block;
}

h2 {
    font-size: 3rem;
    font-weight: 700;
    margin-bottom: 6.25rem
}

p {
    font-size: 1.875rem;
    font-weight: 700;
    line-height: 180%;
    margin-bottom: 3rem;
}

.none {
    display: none;
}

.container {
    margin: 0 auto;
    padding: 0 20px;
    max-width: 1280px;
}

.externalLinks {
    display: inline-block;
    font-size: 1.875rem;
    margin-top: 2rem;
    padding: 2rem;
    font-weight: 500;
    width: 100%;
    text-align: center;
    text-decoration: underline;
    box-sizing: border-box;
}

.externalLinks:hover {
    color: rgb(0, 0, 0, .6);
    font-weight: 500;
}

.externalLinks:active {
    color: rgb(0, 0, 0, .4);
}

.nextPage {
    display: inline-block;
    font-size: 1.875rem;
    margin-top: 3rem;
    width: 100%;
    font-weight: 700;
    text-align: center !important;
    padding: 2rem !important;
    /* border: 3px solid var(--black); */
    box-sizing: border-box;
    background-color: rgb(0, 0, 0, .2);
    transition: all .3s;
}

.nextPage:hover {
    background-color: rgb(0, 0, 0, .1);
}

.nextPage:active {
    background-color: rgb(0, 0, 0, .4);
}

img{
    -webkit-filter:grayscale(100%);
}
.homeContent {
    display: flex;
    margin: 0 auto;
}

.homeContainer {
    width: 65%;
    justify-content: flex-start;
    /* background-color: #ccc; 要做半透明白色*/
    background-color: rgb(255, 255, 255, .8);
    overflow: auto;
    height: 100vh;
    -ms-overflow-style: none;
    /* IE and Edge */
    scrollbar-width: none;
    /*隱藏scroll bar*/
}

.homeContainer::-webkit-scrollbar {
    display: none;
    /*chrome 隱藏scroll bar*/
}

.videoContainer {
    width: 35%;
    height: 100vh !important;
    justify-content: flex-end;
    background-color: #fff;
    overflow: hidden;
}

.insideContent {
    padding: 6.2rem 6rem 0 6rem;
}

.videoContainer video {
    height: 100%;

}

@media (max-width: 682px) {
    .videoContainer {
        position: absolute;
        z-index: -1;
        width: 100%;
    }

    .homeContainer {
        width: 100%;
        justify-content: flex-start;
        overflow: auto;
        height: 100vh;
    }

    .insideContent {
        padding: 7rem 1.5rem 0 1.5rem;
    }
}

@media screen and (max-width: 1068px) {
    .insideContent {
        padding: 7rem 1.5rem 0 1.5rem;
    }
}
.nav {
    width: 65%;
    background-color: rgb(255, 255, 255);
    margin: 0;
    position: fixed;
    justify-content: space-between;
    display: flex;
    z-index: 100;
}

.nav-options {
    padding-left: 4rem;
  display: grid;
  grid-template-columns: repeat(3, auto);
  grid-gap: 50px;
  list-style-type: none;
}

.nav ul li {
    position: relative;
}

.nav ul li a {
    display: inline-block;
    padding: 2.25rem;
    font-weight: 500;
    font-size: 1rem;
    white-space: nowrap;
    transition: background-color .3s ease-in-out;
}

.nav ul li:hover a {
    background-color: #f2f2f2;
}

.nav ul li:active a {
    background-color: #ccc;
}

.navLogo {
    transition: background-color .3s ease-in-out;
}

.navLogo:hover {
    background-color: #f2f2f2;
}

.navLogo:active {
    background-color: #ccc;
}

.navLogo p {
    display: inline-block;
    text-align: left;
    font-weight: 700;
    font-size: 1rem;
    white-space: nowrap;
    padding: 1.2rem 1.2rem 1rem 1rem;
    margin-bottom: 0;
}

.burgerIcon {
    display: none;
    padding: 2.25rem;
}


label {
    font-size: 1.5rem;
    line-height: 2.5rem;
}



@media screen and (max-width: 682px) {
    .nav {
        width: 100%;
       background-color:rgba(255, 255, 255)
    }

}

@media screen and (max-width: 938px) {
    
    
    .burgerIcon {
    display: inline-block;
    }

    .nav-options {
    display: flex;
    width: 100%;
    height: 260px;
    position: absolute;
    top: 89px;
    left: -100%;
    opacity: 0;
    transition: all 0.5s ease;
    flex-direction: column;
    list-style-type: none;
    grid-gap: 0px;
    background-color:rgba(255, 255, 255) ;
  }

  .nav-options.active {
    left: 0;
    opacity: 1;
    transition: all 0.5s ease;
    z-index: 1;
    align-content: center;
    padding-left: 0px;
  }

    .nav ul li{
        text-align: center;
    }

 
}


.contact {
    margin-bottom: 12rem;
}

.contact h2 {
    margin-bottom: 4.375rem;
}

.contactInformation {
    display: flex;
}


.contactInformation ul:first-child {
    margin-right: 6.25rem;
}

.contactInformation ul li:nth-child(odd) {
    margin-bottom: 0.625rem;
    font-weight: 700;
}

.contactInformation ul li:nth-child(even) {
    margin-bottom: 2rem;
    font-weight: 400;
}

@media (max-width: 576px) {
    .contactInformation {
        display: block;
    }

    .contactInformation ul li:nth-child(6) {
        margin-bottom: 6rem;
    }
}
.copyright {
    height: 7.5rem;
    background-color: var(--black);
}

.copyright p {
    font-size: 1rem;
    color: var(--white);
    text-align: center;
    line-height: 7.5rem;
}
.mainPageTitle {
    text-align: center;
    line-height: 90vh;
}

.mainPageTitle h1 {
    display: inline-block;
    height: 95vh;
    font-size: 3.325rem;
}

.mainPageArticle {
    margin-bottom: 10rem;
}

.mainPageExhibition {
    display: flex;
    flex-wrap: wrap;
    margin-bottom: 10rem;
}

.mainPageExhibition a {
    display: inline-block;
    width: 20rem;
    margin-bottom: 3.125rem;
    margin-right: 2rem;
}

.exhibitionPhoto {
    width: 100%;
    overflow: hidden;
}

.exhibitionPhoto img {
    display: block;
    z-index: -1;
    width: 100%;
    object-fit: cover;
    transition: .5s ease-in-out;
}

.mainPageExhibition a:hover img {
    transform: scale(1.2);
}

.mainPageExhibition a h3 {
    font-size: 1.6rem;
}

@media screen and (max-width: 1152px) {
    .mainPageExhibition a {
        width: 100%;
        margin-right: 0;
    }
}
.sidePageContent{
    padding-top: 5rem;
}

.mainPageArticle img{
    padding: 3rem 0;
    width: 100%;
}

.mainPageArticle a{
text-align: left;
padding-left: 0;
}

.courseMap{
    padding: 1.5rem 0;
}


.teacherPage {
    margin-top: 6.25rem;
    margin-bottom: 12.5rem;
}

.teacherCard {
    display: flex;
    width: 100%;
    /* border: 1px solid #ccc; */
    margin-bottom: 6.25rem;
}

.teacherPhoto {
    width: 12.5rem;
}

.teacherInformation {
    padding: 1.5rem 0 1.5rem 0;
    margin-left: 2.725rem;
}

.teacherName {
    font-size: 2.25rem;
    font-weight: 700;
    margin-right: 1.625rem;
}

.teacherTitle {
    font-size: 1.5rem;
    font-weight: 700;
}

.teacherDegree {
    font-size: 1.125rem;
    margin-top: 0.3rem;
}

.teacherEmail {
    font-size: 1.125rem;
    margin-top: 2rem;
}

@media (max-width: 576px) {
    .teacherCard {
        flex-direction: column;
        text-align: center;
    }

    .teacherPhoto {
        margin: 0 auto;
    }

    .teacherInformation {
        margin-left: 0;
    }
}
